@import url(https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;500;600&display=swap);
/* Fonts */

/* Variables */
:root {
    --black: #333;
    --black-light: #666;
    --white: #fff;

    --bg-color: #f5f5f5;
    --border-color: #ddd;

    --primary: #dc5b5b;
    --primary-light: #ffd3d1;

    --fira-sans: 'Fira Sans', sans-serif;
}

/* Styles */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: 'Fira Sans', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    min-height: 100vh;
    background-color: #f5f5f5;
    background-color: var(--bg-color);
    color: #333;
    color: var(--black);
    font-size: 16px;
}

#root {
    display: flex;
    flex-direction: column;
    margin-top: 72px;
}

.mainWrapper {
    display: flex;
    flex-direction: column;
}

h1 {
    font-size: 56px;
}

h2 {
    font-size: 40px;
}

button {
    font-family: 'Fira Sans', sans-serif;
    font-family: var(--fira-sans);
    font-size: 16px;
    line-height: 1;
    text-transform: uppercase;
    background-color: #dc5b5b;
    background-color: var(--primary);
    border: none;
    color: #fff;
    color: var(--white);
    padding: 12px 24px;
    border-radius: 4px;
    cursor: pointer;
    font-weight: 500;
}

button:hover {
    background-color: #ffd3d1;
    background-color: var(--primary-light);
    color: #333;
    color: var(--black);
}

input {
    font-family: 'Fira Sans', sans-serif;
    font-family: var(--fira-sans);
    border: 2px solid #ddd;
    border: 2px solid var(--border-color);
    font-size: 16px;
    outline: none;
    padding: 12px;
    border-radius: 4px;
}

textarea {
    font-family: 'Fira Sans', sans-serif;
    font-family: var(--fira-sans);
    border: 2px solid #ddd;
    border: 2px solid var(--border-color);
    font-size: 16px;
    outline: none;
    padding: 12px;
    border-radius: 4px;
    max-width: 448px;
}

input:hover,
input:focus {
    border-color: #666;
    border-color: var(--black-light);
}

textarea:hover,
textarea:focus {
    border-color: #666;
    border-color: var(--black-light);
}
